import React, { useState } from 'react'
import { FaPencilAlt, FaTrash } from "react-icons/fa";

const Todo = (data) => {

  const [edit, setEdit] = useState(false);
  const [localTodo, setLocalTodo] = useState(data.title);
  const title = data.title

  const toggleEdit = () => {
    setEdit(!edit);
  }

  const deleteTodo = () => {
    let index = data.index;
    let todos = JSON.parse(localStorage.getItem('todos'));
    delete todos[index];
    localStorage.setItem('todos', JSON.stringify(todos));
    data.setTodos(todos);
  }

  const handleChange = (e) => {
    let todo = e.currentTarget.value;
    setLocalTodo(todo);
    console.log(localTodo)
  }

  const handleSubmit = (e) => {
    let index = data.index;
    let todos = JSON.parse(localStorage.getItem('todos'));
    todos[index] = localTodo;
    localStorage.setItem('todos', JSON.stringify(todos));
    data.setTodos(todos);
    setEdit(!edit)
  }

  const editTodo = () => {
    if (edit) {
      return (
        <div>
          <input type="text" value={localTodo} onChange={e => handleChange(e)} />
          <button onClick={handleSubmit}>Submit</button>
        </div>
      )
    }
  }

  const todo = () => {
    if (edit) {
      return (
        <div>
          <input type="text" value={localTodo} onChange={e => handleChange(e)} />
          <button onClick={handleSubmit}>Submit</button>
        </div>
      )
    } else {
      return (
        <div className="todo-container">
          <div><p className="todo-text">{title}</p></div>
          <div><FaPencilAlt className="todo-icon" onClick={toggleEdit} /><FaTrash className="todo-icon" onClick={deleteTodo} /></div>
          {editTodo()}
        </div>
      );
    }
  }

  return (
    <div className="todo-container">
      {/* <div><p>{title}</p></div>
      <div><FaPencilAlt onClick={toggleEdit} /><FaTrash onClick={deleteTodo} /></div>
      <br />
      {editTodo()} */}
      {todo()}
    </div>
  )
}

export default Todo
